import React, { useEffect, useState } from "react";
import { Container, Row, Col } from "reactstrap";
import Band from "../Images/band.jpg";

const Bio = ({ isMobile }) => {
  const [bio, setBio] = useState(null);
  const [bandPic, setBandPic] = useState(null);
  useEffect(() => {
    getBio();
  }, []);

  const getBio = async () => {
    const rawBio = await fetch(
      "https://sheets.googleapis.com/v4/spreadsheets/1gJ1pdsI2H3c4nF83Lw5ROiGM04puiQltMG4wJs5ctRs/values/BIO?alt=json&key=AIzaSyAZ3tHhFqh5HjKl_W3Hp5xGaq5ycI1JNHY"
    );
    const bioRes = await rawBio.json();
    setBandPic(bioRes.values[0][0]);
    bioRes.values.shift();
    setBio(bioRes.values);
  };
  console.log("bandPic", bandPic);
  return (
    <Container>
      <div data-aos="fade-up">
        <h2 className="section-titles"> Bio</h2>
      </div>
      <Row>
        {bandPic && (
          <Col xs="12" lg="6" data-aos={isMobile ? "fade-up" : "fade-right"}>
            <img
              src={bandPic}
              alt="band"
              width="100%"
              style={{ padding: "10px" }}
            />
          </Col>
        )}
        {bio && (
          <Col xs="12" lg="6" data-aos={isMobile ? "fade-up" : "fade-left"}>
            {bio.map((s) => {
              return (
                <p style={{ margin: "15px", textAlign: "justify" }}>{s}</p>
              );
            })}
          </Col>
        )}
      </Row>
    </Container>
  );
};

export default Bio;
